.containers {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  height: 100vh;
  color: whitesmoke;
  min-height: 500px;
  background-size: cover;
  background-attachment: fixed;
  font-family: "Ubuntu", sans-serif;
  width: 100%;
  background: #000000;
  background: -webkit-linear-gradient(to bottom, #434343, #000000);
  background: linear-gradient(to bottom, #434343, #000000);
  margin-top: -40px;
  word-break: break-all;
}

.about {
  word-break: break-all;

  color: whitesmoke;
  height: 100vh;
  min-height: 500px;
  width: 100%;
  background: #3c3b3f;
  background: #000000;
  background: -webkit-linear-gradient(to bottom, #434343, #000000);
  background: linear-gradient(to bottom, #434343, #000000);
}

.titles > p {
  padding-top: 50px;
  color: whitesmoke;
  font-size: 2.5em;
  font-weight: 400;
  text-decoration: underline 2px;
  text-align: center;
  text-underline-offset: 12px;
}
.profiles {
  width: 80%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
}

.profiles > img {
  width: 30%;
}
.textanimation {
  margin: auto;
  width: 90%;
  text-align: justify;
  font-size: 28px;
  height: 50px;
}
.textPart > p {
  text-align: justify;
  width: 90%;
  margin: auto;
  padding-top: 40px;
  font-size: "22px";
  line-height: 22px;
}
.textPart {
  margin-top: 80px;
  padding-bottom: 30px;
}

@media (min-width: 20px) and (max-width: 480px) {
  .containers {
    height: auto;
  }

  .profiles > img {
    width: 50%;
    height: 100%;
  }
  .profiles {
    width: 100%;
    margin: auto;
    display: block;
  }
  .textanimation {
    width: 90%;
    font-weight: 100;
    text-align: justify;
    height: 100px;
  }
  .textPart > p {
    font-size: 19px;
    word-break: break-all;
    /* text-align: center; */
  }
}

@media (min-width: 480px) and (max-width: 990px) {
  .profiles > img {
    width: 30%;
    height: 100%;
  }
  .profiles {
    width: 85%;
    margin: auto;
    gap: 30px;
  }
  .textPart > p {
    font-size: 20px;
    word-spacing: normal;
  }

  .textanimation {
    height: 125px;
  }
}
