.container {
  position: fixed;
  z-index: 999;
  font-family: "Ubuntu", sans-serif;
  transition: all 0.3s ease;
  font-weight: 600;
  top: 0;
  left: 0;
  right: 0;
  background: #222222;
  background: -webkit-linear-gradient(
    to bottom,
    #707070,
    #5c5c5c
  ); 
  background: linear-gradient(
    to bottom,
    #5f5e5e,
    #3d3d3d
  );
}
.containercolorChanges {
  position: fixed;
  z-index: 999;
  font-family: "Ubuntu", sans-serif;
  transition: all 0.3s ease;
  font-weight: 600;
  top: 0;
  left: 0;
  right: 0;
  background: #92bcb5;
  color: #000;
}

.containercolorChanges > .Navbar {
  color: #000;
}

.Navbar {
  width: 87%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 50px;
}
.logo > a {
  text-decoration: none;
  box-sizing: border-box;
  cursor: pointer;
  color: #92bcb5;
  transition: all 0.3s ease;
  font-size: 32px;
  text-align: center;
}

.navbarsection > a,
.hambergoperation > a {
  text-decoration: none;
  padding: 10px;
  cursor: pointer;
  font-size: 20px;
  color: #fff;
  font-weight: 600;
  transition: color 0.3s ease;
}
.hambergoperation > button {
  display: none;
}

.navbarsection > a:hover {
  color: #92bcb5;
  transition: all 0.3s ease;
}
.hamberg {
  display: none;
}

@media (min-width: 20px) and (max-width: 480px) {
  .navbarsection {
    display: none;
  }

  .logo > a {
    font-size: 32px;
    margin-left: -10px;
  }

  .hamberg {
    display: block;
    color: #fff;
    font-size: 30px;
    text-align: center;
    margin-top: 10px;
    margin-right: 10px;
  }

  .hambergoperation > a {
    display: block;
    font-size: 25px;
  }

  .hambergoperation > button {
    display: block;
    font-size: 40px;
    position: absolute;
    right: 0px;
    top: 0px;
  }
  .hambergoperation {
    position: absolute;
    top: 0px;
    left: 0px;
    background: #000;
    width: 100%;
    height: 100vh;
    margin: auto;
    transition: all 0.3s ease;
    padding-top: 50px;
  }
}

@media (min-width: 480px) and (max-width: 990px) {
  .hamberg {
    display: block;
    color: #fff;
    font-size: 30px;
    text-align: center;
    margin-top: 10px;
  }
  .navbarsection {
    display: none;
  }

  .hambergoperation > a {
    display: block;
    font-size: 30px;

    /* height: 100vh; */
  }

  .hambergoperation {
    position: absolute;
    top: 0px;
    left: 0px;
    background: #000;
    width: 100%;
    height: 100vh;
    padding-top: 50px;
    /* margin: auto; */
    transition: all 0.3s ease;
  }
  .hambergoperation > button {
    display: inline;
    font-size: 30px;
    position: absolute;
    right: 0px;
    top: 0px;
  }
}

.containercolorChanges > .Navbar > .logo > a {
  color: teal;
}

.containercolorChanges > .Navbar > .navbarsection > a,
.containercolorChanges > .Navbar > .hambergoperation > a {
  font-weight: 600;
  color: teal;
}
